
.avatar {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  border: 0.01rem solid;
  border-radius: 20rem;
}
.avatar2{
  display: inline-block;
  width: 3rem;
  height: 3rem;
  font-size: 1rem;
  border: 0.01rem solid;
  text-align: center;
  line-height: 3rem;
  border-radius: 20rem;
}
.linkWap {
  display: flex;
  position: fixed;
  flex-direction: row;
  justify-content:center;
  align-items: flex-end;
  width: 100%;
  bottom: 0rem;
  background-color:white;
  z-index: 9999;
}
.linkWap .home :first-child {
  display: none;
}

.linkWap .Myvote :first-child {
  display: none;
}

.linkWap .active :first-child {
  display: inline;
}
.linkWap .active :nth-child(2) {
  display: none;
}
.home {
  flex-grow: 1;
  display:inline-block;
  text-align:center;
}

.Myvote {
  flex-grow: 1;
  display:inline-block;
  text-align:center;
}

.logout {
  flex-grow: 1;
  float: right;
  left: -2rem;
}

